html, body {
	height: 100%; }

body {
	position: relative;
	overflow-x: hidden;
	min-width: 320px; }

.img-responsive {
	max-width: 100%;
	height: auto;
	display: block; }
.logo {
	width: 77px;
	height: 41px;
	display: block;
	svg {
		width: 100%;
		height: 100%; } }

.fRow {
	display: flex;
	align-items: center;
	&--btw {
		justify-content: space-between; }
	&--start {
		justify-content: flex-start; }
	&--center {} }

ul li {
	display: inline-block; }

.ml-auto {
    margin-left: auto!important; }
.mr-auto {
    margin-right: auto!important; }
.w-100 {
	width: 100%; }
.f-row {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	justify-content: center; }

.icon {
	line-height: 1.2;
	position: relative;
	@include tr-fast;
	cursor: default;
	text-align: center;
	.ico {
		vertical-align: middle; }
	>* {
		display: inline; }
	span {
		display: block;
		@include tr-fast; }
	&--xs {
		font-size: 13px;
		.ico {
			width: 13px;
			height:  13px; } }
	&--md {
		font-size: 17px;
		.ico {
			width: 17px;
			height: 17px; } }
	&--lg {
		width: 35px;
		height: 35px;
		line-height: 33px; }
	&--xl {
		width: 52px;
		height: 52px;
		line-height: 52px; }
	&--circle {
		background: #fff;
		border-radius: 50%;
		box-shadow: $bxsh;
		span {
			bottom: -.1rem;
			right: -.1rem; } }
	&--social {
		width: 35px;
		height: 35px;
		line-height: 35px;
		text-align: center; } }

.icon-span {
	background: $green-g;
	border-radius: 50%;
	width: 21px;
	height: 21px;
	line-height: 20px;
	text-align: center;
	position: absolute;
	display: block;
	i {
		color: #fff;
		font-size: 60%;
		&.if-lock {
			display: block;
			margin-top: 5px; } } }

.i-loot {
	text-align: center;
	&_icon {
		border: 2px solid $green;
		background-size: cover;
		background-position: center;
		margin: 0 auto;
		position: relative;
		span {
			top: -.41rem;
			left: -.41rem; }
		i {
			font-size: 70%; } }
	&_name,&_timingFrom {
		font-size: 11px;
		line-height: 13px;
		font-weight: 700; }
	&_name {
		margin: 12px 0 5px;
		font-weight: 700; }
	&_timingFrom,&_timing {
		color: $text-l; }
	&_timing {
		font-weight: 400;
		font-size: 9px;
		line-height: 13px; }
	&_timingFrom {
		i {
			font-size: 9px;
			margin-bottom: 1px; } } }

.colored {
	font-weight: 700;
	&--green {
		@include text-gr(-45deg,#4ad45a,#2cc986); }
	&--orange {
		@include text-gr(-45deg,#ffcd34, #ff9b25); }
	&--orange-rs {
		@include text-gr(-45deg,#ff9b25, #ffcd34); }
	&--red {
		@include text-gr(-45deg, #ff3942,#ed242d); }
	&--blue {
		@include text-gr(-45deg, #339dff, #339dff); } }

.dropdown {
	dt a {
		cursor: pointer;
		&:after {
			content: '\e900';
			font-family: $if;
			color: lighten($icon-l,2%);
			font-size: 9px; }
		span {
			margin: 0 5px; } }
	dd {
		padding-top: 5px;
		a {
			display: block;
			width: 100%;
			padding: 4px 0;
			line-height: 1;
			clear: both;
			color: $text;
			text-align: inherit;
			white-space: nowrap;
			background-color: transparent;
			border: 0;
			margin: 0 !important;
			text-align: left;
			transition: color 250ms cubic-bezier(.27,.01,.38,1.06);
			cursor: pointer;
			&:hover {
				color: $text-l; } } }
	ul {
		position: absolute;
		display: none;
		z-index: 1000;
		padding: 10px 17px 8px;
		border-radius: 3px;
		font-size: 1rem;
		color: #5a6169;
		background-color: #fff;
		border: 1px solid rgba(0,0,0,.05);
		box-shadow: 0 0.5rem 4rem rgba(0,0,0,.11), 0 10px 20px rgba(0,0,0,.05), 0 2px 3px rgba(0,0,0,.06); }
	li {
		display: block; } }


.btn {
	color: #fff;
	font-weight: 700;
	text-align: center;
	line-height: 1;
	border-radius: 5px;
	padding: 0 22px 3px;
	height: 32px;
	line-height: 28px;
	display: block;
	cursor: pointer;
	user-select: none;
	font-size: 13px;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	&--green-rs {
		background: linear-gradient(#4ad45a 0%, #2cc986 100%);
		box-shadow: inset -1px -3px 0 rgba(42,171,67,.5); }
	&--green {
		background: linear-gradient(-45deg, #4ad45a 0%, #2cc986 100%);
		box-shadow: inset 0 -3px 0 rgba(42, 171, 67, 0.5); }
	&--green,&--green-rs {
		@include btn-hover(45deg,#4ad45a,#2cc986, 3%); }
	&--blue {
		background: $blue-g;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.1);
		@include btn-hover(-25deg,#339dff,#2bb2f8, 5%); }
	&--md {
		font-size: 15px;
		height: 40px;
		line-height: 38px; }
	&--lg {
		height: 44px;
		line-height: 42px;
		font-size: 15px; }
	&--xl {
		height: 50px;
		line-height: 48px;
		padding-top: 0;
		padding-bottom: 4px;
		bottom: 0;
		font-size: 17px; }
	&--social {
		display: block;
		text-align: left;
		padding-left: 51px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.1);
		&.steam {
			background-color: #2e3041; }
		&.vk {
			box-shadow: inset 0 -3px 0 rgba(44, 82, 126, 0.5);
			background-color: #4a76a8; }
		&.facebook {
			background-color: #4867aa;
			i {
				font-size: 19px; } }
		&.google {
			background-color: #dd4b39;
			i {
				font-size: 23px; } }
		i {
			color: #fff;
			position: absolute;
			@include center(v);
			left: 13px;
			font-size: 21px;
			margin-top: -2px; } }
	.coin-span {
		span {
			color: #fff; } } }

.loot {
	cursor: pointer;
	position: relative;
	overflow: hidden;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	display: block;
	&_top {
		position: relative; }
	&_title {
		font-weight: 600;
		text-align: center;
		position: relative;
		@include tr-fast; }
	&_cover {
		width: 100%;
		@include cover;
		background-size: cover;
		margin: 0 auto;
		border-radius: 3px;
		box-shadow: 0 2px 5px rgba(79, 82, 92, 0.2); }
	.coin-span {
		@include center(h);
		bottom: -12px;
		margin-right: 5px; }
	&--sm {
		.loot {
			&_cover {
				height: 106px; }
			&_title {
				margin: 6px 0 0 -1px; } } }
	&--md {
		.loot {
			&_top {
				z-index: 2; }
			&_bottom {
				z-index: 1; }
			&_cover {
				height: 168px;
				position: relative; }
			&_title {
				font-size: 13px;
				position: relative;
				span {
					padding: 15px 3px 8px;
					@include tr-fast;
					@include bd-hover($yellow);
					position: relative;
					display: inline-block;
					&:after {
						height: 100px; } } } }
		&:hover {
			.loot_title span {
				color: $yellow;
				border-color: $yellow;
				&:after {
					opacity: .1; } } } }
	&--hot, &--hit, &--new {
			&:after {
				position: absolute;
				display: block;
				color: #fff;
				font-size: {}
				width: 80px;
				height: 20px;
				line-height: 20px;
				transform: rotate(47deg);
				top: 10px;
				right: -22px;
				font-weight: 600;
				font-size: 11px;
				letter-spacing: .06rem;
				text-align: center;
				z-index: 2; } }
	&--hot {
		&:after {
			content: 'HOT';
			background: $red-g;
			box-shadow: 0 4px 8px rgba(249, 51, 59, 0.3); } }
	&--new {
		&:after {
			content: 'NEW';
			background: $green-g;
			box-shadow: 0 4px 8px rgba(61, 207, 110, 0.3); } }
	&--hit {
		&:after {
			content: 'HIT';
			background: $yellow-g;
			box-shadow: 0 4px 8px rgba(255, 170, 41, 0.3); } } }

.coin-span {
	width: 40px;
	display: flex;
	align-items: center;
	max-height: 27px;
	border-radius: 12px;
	cursor: default;
	img {
		margin: 4px 0 0 7px;
		z-index: 1; }
	span {
		z-index: 2; }
	&--bg {
		background: #fff;
		background-image: url(../img/coin.png);
		background-position: .1rem -.03rem;
		background-repeat: no-repeat !important;
		width: 50px;
		height: 23px;
		line-height: 23px;
		box-shadow: 0 4px 8px rgba(255, 173, 0, 0.15);
		span {
			color: $yellow;
			padding-left: 29px; } }
	&--blue,&--green {
		color: #fff;
		text-align: center;
		display: block;
		font-weight: 700; }
	&--blue {
		width: max-content;
		background: $blue;
		letter-spacing: .3px;
		padding: 3px 10px;
		font-size: 13px;
		box-shadow: 0 4px 9px rgba(50, 159, 255, 0.38);
		i {
			color: #fff;
			vertical-align: middle;
			font-size: 99%;
			margin-right: 2px; } }
	&--green {
		background: $green-g;
		width: 52px;
		font-size: 1rem; }
	&--md {
		width: auto;
		img {
			width: 27px;
			height: 27px;
			margin:  0 4px 0 3px; }
		span,strong {
			font-size: 21px;
			font-weight: 700; } }
	&--xl {
		img {
			width: 47px;
			height: 47px;
			margin: 2px 5px 0 0;
			padding: 2px; }
		span {
			font-size: 35px;
			line-height: 1; } } }

[data-tooltip] {
	position: relative;
	z-index: 2000000;
	cursor: pointer;
	&:before, &:after {
		position: absolute;
		visibility: hidden;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
		opacity: 0;
		pointer-events: none;
		@include center(h);
		transform: translateY(15px) translateX(-50%);
		transition: transform 350ms cubic-bezier(.27,.01,.38,1.06), opacity  25ms cubic-bezier(.27,.01,.38,.6), visibility 250ms ease;
		white-space: normal;
		bottom: 120%;
		display: block; }
	&:before {
		width: auto;
		margin-bottom: 5px;
		padding: 6px 9px;
		background-color: #fff;
		font-weight: 700;
		content: attr(data-tooltip);
		text-align: center;
		font-size: 9px;
		line-height: 11px;
		border-radius: 2px;
		box-shadow: 0 3px 6px rgba(46, 48, 65, 0.2); }
	&:after {
		width: 0;
		border-top: 5px solid #fff;
		border-right: 5px solid transparent;
		border-left: 5px solid transparent;
		content: " ";
		font-size: 0;
		line-height: 0;
		filter: drop-shadow(1px 2px 3px rgba(0,0,0,.08)); }
	&:hover {
		&:before, &:after {
		  visibility: visible;
		  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
		  opacity: 1;
		  transform: translateY(0) translateX(-50%); } } }


.pay-item {
	box-shadow: 0 3px 8px rgba(0, 0, 0, 0.09);
	background-color: #fff;
	border-radius: 10px;
	padding: 30px 27px 30px 90px;
	cursor: pointer;
	border: 2px solid transparent;
	@include tr-fast;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	position: relative;
	margin-bottom: 1rem;
	&:hover,&.is-active {
		border-color: rgba(44, 201, 134, 0.34);
		box-shadow: 0 3px 8px rgba(44, 201, 134, 0.09); }
	&:hover {
		background: #f0fff8; }
	&--noIcon {
		padding-left: 0;
		padding-right: 0;
		> div {
			width: 100%;
			text-align: center; } }
	&.is-active {
		i {
			opacity: 1;
			transform: translate(0);
			visibility: visible; } }
	&_str {
		font-weight: 700;
		font-size: 16px; }
	.light {
		color: $text-l;
		font-size: 12px;
		font-weight: 500; }
	i {
		opacity: 0;
		visibility: hidden;
		transition: transform 355ms cubic-bezier(.27,.01,.38,1.06), opacity  250ms cubic-bezier(.27,.01,.38,1.06), visibility  1s;
		position: absolute;
		top: 7px;
		right: 9px;
		transform: translateY(-3px);
		font-size: 19px; }
	&_icon {
		position: absolute;
		top: 50%;
		left: 25px;
		transform: translateY(-50%); } }

.p-block {
	&_heading {
		position: relative;
		margin: 1.65rem 0 1rem;
		padding-left: 43px;
		line-height: 30px;
		&:before {
			content: attr(data-step);
			background: $green-g;
			border-radius: 50%;
			color: #fff;
			height: 30px;
			width: 30px;
			font-size: 1rem;
			left: 0;
			position: absolute;
			text-align: center;
			line-height: 30px; } } }

.user {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	&_right {
		text-align: left; }
	&_balance {
		> span {
			color: #c5c7cf;
