			font-weight: 500;
			line-height: 23px; }
		.coin-span {
			margin: -2px 0 9px -6px;
			img {
				margin-left: 0; }
			span {
				&.light {
					font-size: 11px;
					color: $text-l;
					font-weight: 500;
