				margin-bottom: 9px; } } } }

.avatar {
	box-shadow: 0 3px 7px rgba(58, 212, 118, 0.3);
	background-color: #f1f2f4;
	position: relative;
	width: 45px;
	height: 45px;
	border-radius: 50%;
	@include border-gradient(#2cc986,#4ad45a,1px);
	overflow: hidden;
	@include cover;
	img {
		@include coverdiv;
		height: auto;
		z-index: 2; }
	&_initial {
		z-index: 1;
		i {
			font-size: 48px;
			padding: 0 0 1px 2px;
			margin: 0 auto;
			@include vcenter; } } }
.l-title {
	font-weight: 700;
	padding: 13.5px 0;
	position: relative;
	cursor: default;
	img {
		vertical-align: middle;
		margin: 0 5px 4px 0;
		width: 18px;
		height: 18px; } }

.line-nav {
	width: 100%;
	background: #fff;
	@extend .fRow;
	.l-title {
		flex: 1;
		text-align: center;
		height: 50px;
		line-height: 22px;
		border-bottom: 1px solid #f4f5f7;
		@include tr-fast;
		cursor: pointer;
		@include bdh($yellow);
		&:not(:first-child) {
			border-left: 1px solid #f4f5f7; }
		&:after,&:before {
			content: '';
			position: absolute;
			width: 100%;
			left: 0;
			bottom: -1px;
			@include tr-fast;
			opacity: 0; }
		&:after {
			height: 40%; }
		&:before {
			height: 2px; }
		&:hover, &.is-active {
			&:after {
				opacity: .07; }
			&:before {
				opacity: 1; } } } }
.box-sm-title {
	font-size: 11px;
	font-weight: 700;
	img {
		width: 14px;
		height: 14px;
		object-fit: cover;
		border-radius: 50%;
		display: inline-block;
		vertical-align: middle;
		margin-bottom: 2px; }
	span {
		margin-left: 3px;
		display: inline-block; } }

.game-card {
