	visibility: visible;
	height: auto; }

.mess {
	background: #fff;
	box-shadow: 0 2px 9px rgba(197, 199, 207, 0.47);
	padding: 20px;
	display: flex;
	align-items: center;
	position: relative;
	justify-content: center;
