			color: #c5c7cf; } } }

.icon-with-desc {
	justify-content: flex-start;
	display: flex;
	align-items: center;
	cursor: default;
	img {
		vertical-align: middle;
		margin-bottom: 3px; }
	.icon-span {
		width: 19px;
		height: 19px; }
	p {
		font-size: 11px;
		line-height: 1.35;
		margin-left: 20px; } }

.video {
	position: relative;
	&:hover {
		.video_control i {
			opacity: .6; } }
	&:active {
		.video_control i {
			opacity: 1; } }
	&_preview {
		@extend .img-responsive; }
	&_control {
		text-align: center;
		cursor: pointer;
		pointer-events: none;
		@include vcenter;
		> * {
			display: block;
			color: #fff; }
		span {
			text-transform: uppercase;
			font-size: 13px; }
		i {
			font-size: 39px;
			margin-top: 3px;
			opacity: 1;
			transition: all .47s ease-out; } } }

.b-overlay {
	&:before {
		content: '';
		@include coverdiv;
		z-index: 0;
		background-color: rgba(46,48,65,.5); } }


.game-aside {
	margin-right: 20px;
	.wrap {
		overflow: hidden; }
	&_top,&_middle,&_bottom {
		position: relative; }
	&_top {
		padding-bottom: 95%; }
	&_middle {
		padding: 22px 25px 30px;
		border-bottom: $bd-light; }
	&_bottom {
		padding: 20px;
		.video {
			height: 142px;
			overflow: hidden;
			border-radius: 5px; } }
	&_loot {
		@include vcenter;
		.loot {
			width: 118px; }
		.coin-span--blue {
			margin-top: 8px;
			box-shadow: none;
			background: $blue-g;
			padding-bottom: 4px; } }
	&_cover {
		@include coverdiv;
		@include cover;
		opacity: .9; }
	.game-card {
		&_title {
			padding-bottom: 5px;
			+ .light {
				color: #c5c7cf; } }
		.light {
			font-weight: 600; }
		&_row {
			padding-top: 10px;
			.light {
				line-height: 2.3; }
			i {
				vertical-align: baseline;
				margin-right: 7px;
				&.if-lock {
					color: $blue; } } } } }

.game-panel {
	background: #fff;
	padding-bottom: 10vh;
	.l-title,.game-panel_col {
		@extend .fRow; }
	&_head {
		.if-star {
			width: 23px;
			height: 23px;
			font-size: 12px;
			line-height: 23px;
			text-align: center;
			color: #fff;
			border-radius: 50%;
			padding-right: 1px;
			background: $blue;
			box-shadow: 0 1px 4px 2px rgba(39,168,254,.29); }
		.l-title {
			justify-content: center;
			i {
				margin: 0 11px 0 0; }
			.if-x2 {
				font-size: 33px;
				margin-right: 5px;
				text-shadow: 0px 2px 10px rgba(252, 53, 62, 0.29); } } }
	&_inform {
		margin: 0 !important;
		.coin-span img {
			margin-left: 0; }
		.col {
			font-weight: 700; } }
	&_row {
		padding: 12px 20px 0;
		width: 100%;
		position: relative; }
	&_col {
		padding: 0 18px;
		border-radius: 5px;
		justify-content: space-between;
		@include rmin(1119) {
			height: 40px; } }
	&_chances {
		background: $bg-gray;
		margin-right: 5px;
		> span {
			margin-right: 10px; } }
	&_open-price {
		background: rgba(255,173,0,.1);
		margin-left: 5px; }
	.mess {
		justify-content: space-between;
		box-shadow: none;
		border: $bd-light;
		&_close i {
			margin-right: 0;
			margin-bottom: 1px; }
		&_text {
			i {
				margin-right: 10px; }
			span {
				font-weight: 600; } }
		span {
			font-size: 13px; } }
	&_action-place {
		min-height: 30vh;
		background: $bg-gray;
		position: relative;
		i {
			font-size: 50px;
			@include vcenter; } } }
