[data-tooltip] {
  position: relative;
  z-index: 2000000;
  cursor: pointer;
}

[data-tooltip]:before, [data-tooltip]:after {
  position: absolute;
  visibility: hidden;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  opacity: 0;
  pointer-events: none;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transform: translateY(15px) translateX(-50%);
  transition: transform 350ms cubic-bezier(0.27, 0.01, 0.38, 1.06), opacity 25ms cubic-bezier(0.27, 0.01, 0.38, 0.6), visibility 250ms ease;
  white-space: normal;
  bottom: 120%;
  display: block;
}

[data-tooltip]:before {
  width: auto;
  margin-bottom: 5px;
  padding: 6px 9px;
  background-color: #fff;
  font-weight: 700;
  content: attr(data-tooltip);
  text-align: center;
  font-size: 9px;
  line-height: 11px;
  border-radius: 2px;
  box-shadow: 0 3px 6px rgba(46, 48, 65, 0.2);
}

[data-tooltip]:after {
  width: 0;
  border-top: 5px solid #fff;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  content: " ";
  font-size: 0;
  line-height: 0;
  filter: drop-shadow(1px 2px 3px rgba(0, 0, 0, 0.08));
}

[data-tooltip]:hover:before, [data-tooltip]:hover:after {
  visibility: visible;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  opacity: 1;
  transform: translateY(0) translateX(-50%);
}

