  text-align: center;
  font-weight: 700;
  margin-top: 17px;
}

.win-b_cover {
  width: 118px;
  height: 168px;
  margin: 0 20px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  box-shadow: 1px 2px 12px rgba(26, 27, 35, 0.8);
}

.c-chance {
  justify-content: space-between;
  padding: 15px;
  background-color: #f5f6f7;
  font-weight: 700;
  border-radius: 7px;
}

.profile-page .wrap {
  margin-bottom: 20px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
}

.profile-page_left-block {
  padding-top: 20px;
  margin: 0 130px 0 50px;
}

.profile-page_right-block {
  flex: 1 0;
}

.profile-page .tab-btns .l-title:hover, .profile-page .tab-btns .l-title.is-active {
  color: #ffad00;
}

.profile-page .tab-btns .l-title:after {
  background: linear-gradient(to top, #ffad00 0%, rgba(255, 255, 255, 0.6) 100%);
}

.profile-page .tab-btns .l-title:before {
  background: #ffad00;
}

.profile-page .tab-wrap {
  padding: 25px 40px 35px;
}

.profile-page .tab-wrap .col-md-6 {
  padding: 0 10px !important;
}

@media only screen and (min-width: 1199px) {
  .profile-page .tab-wrap {
    min-height: 640px;
  }
}

.profile-page .game-card {
  margin: 10px 0;
}

.profile-page .stat-item {
  text-align: center;
}

.profile-page .stat-item .colored {
  font-size: 40px;
  font-weight: 700;
}

.profile-page .stat-item .light {
  color: #8b8fa0;
  font-size: 15px;
  padding-top: 2px;
  display: block;
}

.profile-page .table {
  margin-top: 60px;
}

.referal-panel {
  box-shadow: 0 3px 10px rgba(79, 82, 92, 0.1);
  padding: 22px 30px 30px;
  border-radius: 10px;
  width: 100%;
}

.referal-panel_title {
  padding-left: 30px;
  position: relative;
  font-weight: 700;
}

.referal-panel_title img {
  position: absolute;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -1.5px;
}

.referal-panel p {
  color: #8b8fa0;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.2;
  padding-top: 7px;
}

.referal-panel form {
  padding-top: 18px;
}

#copy .fRow, #copy .line-nav, #copy .game-panel .l-title, .game-panel #copy .l-title, #copy .game-panel .game-panel_col, .game-panel #copy .game-panel_col, #copy .c-chance {
  height: 39px;
  align-items: flex-start;
}

.copy-btn {
  width: 40px;
  padding: 11px 1rem 4px 11px;
  margin-left: 10px;
  cursor: pointer;
  transition: transform 350ms cubic-bezier(0.27, 0.01, 0.38, 1.06), opacity 250ms cubic-bezier(0.27, 0.01, 0.38, 1.06), visibility 1s, background 320ms cubic-bezier(0.27, 0.01, 0.38, 1.06), border 20ms cubic-bezier(0.27, 0.01, 0.38, 1.06), box-shadow 1s ease-out;
}

.copy-btn:hover {
  border-color: #c5c7cf;
  background: #c5c7cf;
}

.copy-btn:before, .copy-btn:after {
  bottom: 100%;
}

.copy-btn i {
  color: #2e3041;
  pointer-events: none;
  vertical-align: top;
}

.copy-btn:active, .copy-btn:hover, .copy-btn:focus {
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
}

.copy-btn:focus {
  border-color: #c5c7cf !important;
}

.copy-btn.copied:after, .copy-btn.copied:before {
  opacity: 1;
  visibility: visible;
}

.header, .footer {
  width: 100%;
}

.header {
  box-shadow: 0 5px 20px rgba(79, 82, 92, 0.1);
  background: #fff;
  top: 0;
  z-index: 9999;
}

.header:active {
  box-shadow: 0 5px 20px rgba(79, 82, 92, 0.1) !important;
}

.header_nav {
  margin-left: 22px;
  margin-top: 3px;
}

.header_nav > li {
  cursor: pointer;
}

.header_nav > li:not(:first-child) {
  margin-left: 27px;
}

.header_nav > li:hover > a {
  color: #8b8fa0;
}

@media only screen and (min-width: 768px) {
  .header_nav dl {
    width: 69px;
  }
}

.header_nav li a,
.header_nav dt span {
  font-size: 13px;
  font-weight: 600;
}

.header_nav dt a span {
  margin: 0 3px 0 4px;
}

.header_nav i {
  font-size: 16px;
  margin: 0 3px 3px 0;
  display: inline-block;
}

.header_nav i.if-planet {
  margin-bottom: 3px;
}

.header_socials {
  margin-right: 20px;
}

.header_socials .if-vk,
.header_socials .if-google {
  font-size: 19px;
}

.header_socials .if-vk {
  margin: 0 8px 0 4px;
}

.header_socials .if-facebook {
  font-size: 14px;
  margin: 0 5px;
}

.header_stat {
  margin-left: 60px;
}

.header .stat-item {
  margin-right: 25px;
  cursor: default;
}

.header .stat-item span.light {
  display: block;
  font-size: 9px;
  color: #8b8fa0;
  margin-top: -1px;
}

.grid + .footer {
  padding: 52px 0 50px;
}

.inner-section + .footer {
  padding: 20px 0;
}

.footer {
  position: absolute;
  bottom: 0;
  font-size: 13px;
  color: #8b8fa0;
  padding: 50px 0;
  background: #f6f7fb;
}

.footer a {
  font-size: 13px;
  color: #8b8fa0;
}

.footer_copy {
  margin: 0 70px 0 35px;
}

.footer_logo {
  margin-bottom: -4px;
}

.footer_links a {
  margin-right: 25px;
}

.footer a.icon {
  margin-left: 5px;
}

.footer a.icon i {
  color: #fff;
  font-size: 15px;
}

.footer a.icon i.if-vk {
  margin-right: 3px;
  font-size: 17px;
}

.loots-tape {
  transition: padding .48s linear;
  overflow-x: hidden;
}

.loots-tape_menu-item {
  display: inline-block;
  margin-right: 9px;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  font-size: 11px;
  font-weight: 700;
  color: #8b8fa0;
}

.loots-tape_menu-item.hide-ld {
  color: #c5c7cf;
}

.loots-tape_menu-item.hide-ld:hover {
  border-color: transparent;
}

.loots-tape_menu-item.hide-ld.is-active {
  border-color: #c5c7cf;
  color: #c5c7cf;
}

.loots-tape_menu-item:hover {
  border-bottom: 2px solid #8b8fa0;
}

.loots-tape_menu-item.is-active {
  color: #ffad00;
  border-color: #ffad00;
}

.loots-tape_menu-item.hide_active_tab {
  color: #8b8fa0;
  border-color: transparent;
}

.loots-tape_volume {
  cursor: pointer;
}

.loots-tape_volume .icon-s-on {
  display: none;
}

.loots-tape_volume .icon-s-off {
  display: block;
}

.loots-tape_volume.volume-on .icon-s-on {
  display: block;
}

.loots-tape_volume.volume-on .icon-s-off {
  display: none;
}

.loots-tape_aside {
  padding: 15px 4px 32px 10px;
  z-index: 40;
  flex-shrink: 0;
  width: 65px;
  background: #f6f7fb;
}

.loots-tape_head {
  padding: 10px 0 15px;
}

.loots-tape_wrap {
  padding-left: 13.5px;
  background: #f6f7fb;
}

.loots-tape_wrap.is-visible {
  overflow: visible;
}

.loots-tape_tape {
  width: 100%;
  white-space: nowrap;
  padding-left: 8px;
  z-index: 20;
}

.loots-tape_tape:hover {
  z-index: 30;
}

.loots-tape [data-tooltip]:before {
  width: 130%;
}

.loots-tape [data-tooltip]:before, .loots-tape [data-tooltip]:after {
  bottom: 102%;
  transform: translateX(-50%);
}

.loots-tape .loot {
  display: inline-block;
  margin: 0 5.5px;
  position: relative;
  overflow: visible;
  z-index: 0;
  width: 75px;
}

.loots-tape .loot_title {
  overflow: hidden;
}

.loots-tape .loot_title:before {
  content: '';
  position: absolute;
  width: 7%;
  height: 100%;
  bottom: 0;
  right: 0;
  background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(97%, #fff));
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0, #fff 97%);
  z-index: 1;
}

.loots-tape .loot_cover {
  border: none;
}

.loots-tape .loot > a {
  display: block;
}

.loot_hide-info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  height: 106px;
  display: table;
  overflow: hidden;
}

.loot_hide-info .i-loot {
  background: rgba(46, 48, 65, 0.78);
  transition: all .35s linear;
  opacity: 0;
  display: table-cell;
  vertical-align: middle;
  box-shadow: -2px 18px 20px 20px rgba(46, 48, 65, 0.78);
}

.loot_hide-info .i-loot_name, .loot_hide-info .i-loot_timing {
  color: #fff;
}

.loot--hov:hover .loot_title {
  color: #8b8fa0;
}

.loot--hov:hover .i-loot {
  opacity: 1;
}
