@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin md {
  @include r(1023) {
    @content; } }

@mixin sm {
  @include r(767) {
    @content; } }

@mixin xs {
  @include r(599) {
    @content; } }

.container {
  padding: 0 !important; }

.p0 {
  padding: 0; }

.flex-r {
  display: flex;
  align-items: center;
  justify-content: space-between; }

@mixin bd-h($bdh,$bdc) {
  border-bottom: $bdh solid transparent;
  &:hover {
    border-color: $bdc; } }


@mixin bd-h2($bd-w,$bdc) {
  border-bottom: $bd-w solid $bdc;
  &:hover {
    border-color: transparent; } }

@mixin text-gr($dr,$from-color, $to-color) {
  background: linear-gradient($dr, $from-color 0%, $to-color 100%);
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent !important; }

@mixin gr($dr,$from-color, $to-color) {
  background: linear-gradient($dr, $from-color 0%, $to-color 100%); }

@mixin border-gradient($from, $to, $weight: 0) {
  $mix-main: mix($from, $to);
  $mix-sub-from: mix($mix-main, $from);
  $mix-sub-to: mix($mix-main, $to);
  box-shadow: 0 1px 0 $weight rgba($mix-sub-to, .25), 0 -1px 0 $weight rgba($mix-sub-from, .25), 1px 0 0 $weight rgba($mix-sub-to, .25), -1px 0 0 $weight  rgba($mix-sub-from, .25), 1px -1px 0 $weight rgba($mix-main, .5), -1px 1px 0 $weight rgba($mix-main, .5), 1px 1px 0 $weight rgba($to, .75), -1px -1px 0 $weight rgba($from, .75), }

@mixin bd-hover ($color) {
  border-bottom: 2px solid transparent;
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 42px;
    left: 0;
    bottom: 0;
    opacity: 0;
    @include tr-fast;
    @include gr(to top, $color, rgba(255,255,255,.6)); } }

@mixin j-hover ($o, $h,$t,$to, $color, $color2) {
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: $h;
    left: 0;
    top: $t;
    opacity: $o;
    @include tr-fast;
    background-image: linear-gradient(to $to, $color 0%, $color2 100%); } }

@mixin bdh ($colore) {
  &:hover, &.is-active {
    color: $colore; }
  &:after {
    @include gr(to top, $colore, rgba(255,255,255,.6)); }
  &:before {
    background: $colore; } }


.swatch-green,
.swatch-blue,
.swatch-yellow,
.swatch-red {
  cursor: pointer; }


.swatch-green:hover {
  background: $green-h; }

.swatch-red:hover {
  background: $red-h; }

.swatch-yellow:hover {
  background: $yellow-h; }

.swatch-blue:hover {
  background: $blue-h; }

@mixin hide {
  opacity: 0;
  visibility: hidden;
  height: 0;
  overflow: hidden;
  @include tr-fast; }

@mixin btn-hover ($deg, $c1,$c2,$per) {
  &:hover {
      background: linear-gradient($deg, lighten($c2,$per) 0%, lighten($c1,$per) 100%); } }
