*::-webkit-input-placeholder {
	color: $text;
	opacity: 1;
	font-weight: 700; }

*:-moz-placeholder {
	color: $text;
	opacity: 1;
	font-weight: 700; }

*::-moz-placeholder {
	color: $text;
	opacity: 1;
	font-weight: 700; }

*:-ms-input-placeholder {
	color: $text;
	opacity: 1; }

::selection {
	background-color: darken($bg-gray,10%);
	color: $text; }


label {
	display: inline-block;
	margin-bottom: .5rem;
	span {
		&:not(.normal-mess) {
			display: none; }
		&.text-mess {
			color: $text !important; } } }
input {
	color: $text;
	font-weight: 600;
	width: 100%;
	&.btn {
		color: #fff !important;
		border: none;
		&--green {
			box-shadow: inset 0 -3px 0 rgba(42,171,67,.5); } } }

.form-group {
    margin-bottom: 1rem;
    position: relative;
    .c-s {
    	position: absolute;
    	right: 28px;
    	@include center(v);
    	margin-top: 18px;
    	font-size: 16px;
    	color: $text; } }

.col-form-label {
	padding-top: calc(.5rem + 1px);
	padding-bottom: calc(.5rem + 1px);
	line-height: 1.5;
	margin-bottom: 0;
	font-size: 13px;
	font-weight: 600; }

.form-control {
	display: block;
	height: auto;
	padding: .5rem 1rem;
	font-size: .95rem;
	line-height: 1.5;
	background-color: #fff;
	border: 2px solid #c5c7cf;
	will-change: border-color,box-shadow;
	border-radius: .375rem;
	box-shadow: none;
	transition: box-shadow 250ms cubic-bezier(.27,.01,.38,1.06),border 250ms cubic-bezier(.27,.01,.38,1.06);
	&:focus {
		background-color: #fff;
		border-color: #2bb2f8; } }


// validation stuff
.form-group {
	&.is-valid {
		label, input {
			color: $green; }
		input {
			border-color: $green; }
		label span {
			&:not(.valid-mess) {
				display: none; }
			&.valid-mess {
				display: block; } } }
	&.is-invalid {
		input {
			border-color: $red; }
		label {
			color: $red;
			span {
				&:not(.invalid-mess) {
					display: none; }
				&.invalid-mess {
					display: block; } } } } }
