.header,.footer {
	width: 100%; }

.header {
	box-shadow: $bxsh;
	background: #fff;
	top: 0;
	z-index: 9999;
	&:active {
		box-shadow: $bxsh !important; }
	&_nav {
		margin-left: 22px;
		margin-top: 3px;
		> li {
			cursor: pointer;
			&:not(:first-child) {
				margin-left: 27px; }
			&:hover > a {
				color: #8b8fa0; } }

		dl {
			@include rmin(768) {
				width: 69px; } }
		li a,
		dt span {
			font-size: 13px;
			font-weight: 600; }
		dt a span {
			margin: 0 3px 0 4px; }
		i {
			font-size: 16px;
			margin: 0 3px 3px 0;
			display: inline-block;
			&.if-planet {
				margin-bottom: 3px; } } }
	&_socials {
		margin-right: 20px;
		.if-vk,
		.if-google {
			font-size: 19px; }
		.if-vk {
			margin: 0 8px 0 4px; }
		.if-facebook {
			font-size: 14px;
			margin: 0 5px; } }
	&_stat {
		margin-left: 60px; }
	.stat-item {
		margin-right: 25px;
		cursor: default;
		span {
			&.light {
				display: block;
				font-size: 9px;
				color: $text-l;
				margin-top: -1px; } } } }

.grid + .footer {
	padding: 52px 0 50px; }
.inner-section + .footer {
	padding: 20px 0; }

.footer {
	position: absolute;
	bottom: 0;
	font-size: 13px;
	color: $text-l;
	padding: 50px 0;
	background: #f6f7fb;
	a {
		font-size: 13px;
		color: $text-l; }
	&_copy {
		margin: 0 70px 0 35px; }
	&_logo {
		margin-bottom: -4px; }
	&_links {
		a {
			margin-right: 25px; } }
	a.icon {
		margin-left: 5px;
		i {
			color: #fff;
			font-size: 15px;
			&.if-vk {
				margin-right: 3px;
				font-size: 17px; } } } }

.loots-tape {
	transition: padding .48s linear;
	overflow-x: hidden;
	&_menu-item {
		display: inline-block;
		margin-right: 9px;
		border-bottom: 2px solid transparent;
		cursor: pointer;
		font-size: 11px;
		font-weight: 700;
		color: $text-l;
		&.hide-ld {
			color: #c5c7cf;
			&:hover {
				border-color: transparent; }
			&.is-active {
				border-color: #c5c7cf;
				color: #c5c7cf; } }
		&:hover {
			border-bottom: 2px solid #8b8fa0; }
		&.is-active {
			color: $yellow;
			border-color: $yellow; }
		&.hide_active_tab {
			color: $text-l;
			border-color: transparent; } }
	&_volume {
		cursor: pointer;
		.icon-s-on {
			display: none; }
		.icon-s-off {
			display: block; }
		&.volume-on {
			.icon-s-on {
				display: block; }
			.icon-s-off {
				display: none; } } }
	&_aside {
		padding: 15px 4px 32px 10px;
		z-index: 40;
		flex-shrink: 0;
		width: 65px;
		background: $bg-gray; }
	&_head {
		padding: 10px 0 15px; }
	&_wrap {
		padding-left: 13.5px;
		background: $bg-gray;
		&.is-visible {
			overflow: visible; } }
	&_tape {
		width: 100%;
		white-space: nowrap;
		padding-left: 8px;
		z-index: 20;
		&:hover {
			z-index: 30; } }
	[data-tooltip] {
		&:before {
			width: 130%; }
		&:before,&:after {
			bottom: 102%;
			transform: translateX(-50%); } }
	.loot {
		display: inline-block;
		margin: 0 5.5px;
		position: relative;
		overflow: visible;
		z-index: 0;
		width: 75px;
		&_title {
			overflow: hidden;
			&:before {
				content: '';
				position: absolute;
				width: 7%;
				height: 100%;
				bottom: 0;
				right: 0;
				background: -webkit-gradient(linear,left top,right top,from(rgba(255,255,255,0)),color-stop(97%,#fff));
				background: linear-gradient(to right,rgba(255,255,255,0) 0,#fff 97%);
				z-index: 1; } }
		&_cover {
			border: none; }
		>a {
			display: block; } } }


.loot {
	&_hide-info {
		@include coverdiv;
		height: 106px;
		display: table;
		overflow: hidden;
		.i-loot {
			background: rgba(46, 48, 65, .78);
			transition: all .35s linear;
			opacity: 0;
			display: table-cell;
			vertical-align: middle;
			box-shadow: -2px 18px 20px 20px rgba(46,48,65,.78);
			&_name,&_timing {
				color: #fff; } } }
	&--hov {
		&:hover {
			.loot_title {
				color: $text-l; }
			.i-loot {
				opacity: 1; } } } }
