* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	&:before, &:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box; } }

html {
	font-size: 15px; }

body {
	@include css-lock(15, 15, 18, 18);
	font-family: $font;
	color: $text;
	font-weight: 600;
	line-height: 1.5; }

$a-tags: 'a, a:active, a:hover, a:visited';
#{$a-tags} {
  text-decoration: none; }

h1 {
	font-size: 3.052rem;
	line-height: 3rem; }
h2 {
	font-size: 2.441rem;
	line-height: 2.25rem; }
h3 {
	font-size: 21px;
	line-height: 1.2rem; }
p {
	font-size: 1rem;
	text-align: left; }

strong {
	font-weight: 700; }


a.btn {
	&:hover,&:active,&:visited {
		color: #fff; } }

a {
	color: $text;
	border: none;
	text-decoration: none;
	&:hover {
		color: $text; }
	&.link {
		&--green {
			color: $green; }
		&--orange {
			color: $orange;
			border-bottom: 1px solid $orange;
			padding-bottom: 2px;
			font-weight: 700;
			&:hover {
				border-color: transparent; } }
		&--green-bd {
			color: $green2;
			display: block;
			line-height: 1.3;
			width: max-content;
			border-bottom: 1px solid lighten($green2,32%);
			&:hover {
				border-color: transparent; } } }
	&.icon {
		display: inline-block;
		cursor: pointer; } }

.t-center {
	text-align: center; }

.t-upp {
	text-transform: uppercase; }
