@font-face {
  font-family: '#{$if}';
  src:  url('#{$icomoon-font-path}/#{$if}.eot?hkq42p');
  src:  url('#{$icomoon-font-path}/#{$if}.eot?hkq42p#iefix') format('embedded-opentype'),  url('#{$icomoon-font-path}/#{$if}.ttf?hkq42p') format('truetype'),    url('#{$icomoon-font-path}/#{$if}.woff?hkq42p') format('woff'),    url('#{$icomoon-font-path}/#{$if}.svg?hkq42p##{$if}') format('svg');
  font-weight: normal;
  font-style: normal; }

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$if}' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  vertical-align: middle;
  text-transform: none;
  display: inline-block;
  line-height: 1;
  color: $icon-l;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }


