$font: 'Montserrat', sans-serif;
$text: #2e3041;
$text-l: #8b8fa0;
$icon-l: #cdced1;
$bg-gray: #f6f7fb;

$green: #2dca84;
$green2: #3bb54b;
$green-g: linear-gradient(-45deg, #4ad45a 0%, #2cc986 100%);
$blue: #30a4fc;
$blue-g: linear-gradient(-30deg, #339dff 0%, #2bb2f8 100%);
$yellow: #ffad00;
$yellow-g: linear-gradient(-45deg, #ff9b25 0%, #ffcd34 100%);
$red: #f9323b;
$red-g: linear-gradient(-45deg, #ff3942 0%, #ed242d 100%);

$if: "lootcamp" !default;
$icomoon-font-path: "../fonts/" !default;

// hover
$per: 5%;
$green-h: #08c508 !important;
$blue-h: lighten(blue, $per) !important;
$red-h: red !important;
$yellow-h: #ffe700 !important;


$ggap: 30px;

$bd: 2px solid #f4f5f7;
$bd-light: 1px solid #f4f5f7;
$bxsh: 0 5px 20px rgba(79, 82, 92, 0.1);
$bxsh-l: 0 3px 10px rgba(79, 82, 92, 0.1);
$tr1: all .27s ease-out;
$tr2: all .12s ease-out;



// bootstrap
$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 1200px, xl: 1200px);
$container-max-widths: (sm: 640px, md: 720px, lg: 960px, xl: 1170px);
$dropdown-border-radius: 0;

