.app {
	position: relative;
	min-height: 100vh;
	background: $bg-gray;
	overflow-x: hidden; }
.outer-container {
	padding: 0 20px;
	&.p-15 {
		padding-top: 15px;
		padding-bottom: 15px; } }

.inner-container {
	width: 72%;
	margin: 0 auto;
	@include rmin(1600) {
		width: 60%; } }

.inner-section {
	padding: 65px 0 150px;
	z-index: 99999;
	> * {
		z-index: 1; } }

.page-content {
	// z-index: -1
	position: relative; }

.wrap {
	width: 100%;
	background: #fff;
	box-shadow: $bxsh;
	border-radius: 10px;
	position: relative;
	&--30 {
		padding: 30px; }
	&--45 {
		padding: 45px 60px; } }

.loots-tape {
	z-index: 9999; }

.footer {
	z-index: 10; }

.grid {
	z-index: 11; }

.features {
	padding: 50px 0 55px;
	transition: all .5s ease-out;
	&.no-p {
		padding: 10px 0 60px; } }

.grid {
	position: relative;
	padding-bottom: 40px;
	box-shadow: $bxsh;
	background: #fff;
	&_content > .outer-container {
		padding-right: 0; }
	.line-nav {
		margin-bottom: -50px;
		transform: translate3d(0, -100%, 0);
		transition: all .4s;
		position: absolute;
		box-shadow: none;
		top: 0;
		left: 0;
		z-index: 1;
		.l-title {
			font-size: 13px; } }
	&_top {
		z-index: 5;
		position: relative;
		&.is-sticky {
			position: fixed;
			top: 0;
			height: 50px;
			left: 0;
			right: 0;
			.line-nav {
				transform: translate3d(0, 0, 0);
				box-shadow: $bxsh; } }
		&.is-normal .line-nav {
			transition: transform 0ms; } }
	&_block {
		margin-bottom: 15px;
		.l-title {
			padding: 20px 0 25px; }
		&:not(:first-child) {
			.l-title {
				padding-top: 5px; } } }
	&_block-heading {
		padding-left: $ggap;
		.l-title img {
			width: 23px;
			height: 23px; } }
	&_content {
		margin-top: 50px;
		z-index: 2; }
	&_row {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		margin: 0;
		padding: 0 $ggap/2;
		&:after {
			content: '';
			flex: auto; } }
	&_item {
		-webkit-box-flex: 0;
		-ms-flex: 0 0 11.1%;
		flex: 0 0 11.1%;
		max-width: 11.1%;
		width: 100%;
		padding: 0 15px;
		margin: 0;
		@include rmin(1600) {
			-ms-flex: 0 0 8.333%;
			flex: 0 0 8.3333%;
			max-width: 8.3333%; }
		@include r(1290) {
			-ms-flex: 0 0 14.28%;
			flex: 0 0 14.28%;
			max-width: 14.28%; }
		@include r(992) {
			-ms-flex: 0 0 20%;
			flex: 0 0 20%;
			max-width: 20%; }
		@include r(768) {
			-ms-flex: 0 0 25%;
			flex: 0 0 25%;
			max-width: 25%; }
		@include r(640) {
			-ms-flex: 0 0 33.33%;
			flex: 0 0 33.33%;
			max-width: 33.33%; }
		@include r(520) {
			-ms-flex: 0 0 50%;
			flex: 0 0 50%;
			max-width: 50%; } }
	.loot {
		margin-bottom: 12px;
		max-width: 118px;
		margin: 0 auto 20px;
		&_cover {
			border-top: 3px solid #24242b;
			border-bottom: 3px solid #24242b;
			border-right: 4px solid #24242b;
			border-left: none;
			background-color: #24242b;
			@include r(992) {
				background-position-y: top; } } } }

.pay-items-container {
	margin-bottom: -.9rem; }

.bottom-informPanel {
	background: $bg-gray;
	width: 100%;
	padding: 20px 25px;
	margin: 15px 0 30px;
	border-radius: 10px;
	justify-content: space-between;
	>div {
		width: 23%;
		line-height: 1.3; }
	p {
		font-size: 11px;
		padding-top: 5px;
		font-weight: 600; }
	strong {
		font-size: 21px; }
	.coin-span--xl {
		justify-content: center;
		margin: -2px auto 5px;
		padding-right: 15px; }
	.gray-panel {
		background: #e9ebf3;
		padding: 11px 15px;
		border-radius: 10px; } }

.p-block {
	.btn--xl {
		@include rmin(1199) {
			width: 266px; } } }

.win-b {
	&_price {
		font-weight: 700;
		font-size: 13px;
		.coin-span {
			box-shadow: 0 4px 9px #dbccb9;
			margin: 5px 0 0 auto;
			span {
				color: $text; } } }
	&_top {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&_title {
		font-size: 17px;
		text-align: center;
		font-weight: 700;
		margin-top: 17px; }
	&_cover {
		width: 118px;
		height: 168px;
		margin: 0 20px;
		position: relative;
		@include cover;
		box-shadow: 1px 2px 12px rgba(26,27,35,.8); } }

.c-chance {
	@extend .fRow;
	justify-content: space-between;
	padding: 15px;
	background-color: #f5f6f7;
	font-weight: 700;
	border-radius: 7px; }

.profile-page {
	.wrap {
		margin-bottom: 20px;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: center; }
	&_left-block {
		padding-top: 20px;
		margin: 0 130px 0 50px; }
	&_right-block {
		flex: 1 0; }
	.tab-btns {
		.l-title {
			@include bdh($yellow); } }
	.tab-wrap {
		padding: 25px 40px 35px;
		.row {
			@extend .no-gutters; }
		.col-md-6 {
			padding: 0 10px !important; }
		@include rmin(1199) {
			min-height: 640px; } }
	.game-card {
		margin: 10px 0; }
	.stat-item {
		text-align: center;
		.colored {
			font-size: 40px;
			font-weight: 700; }
		.light {
			color: $text-l;
			font-size: 15px;
			padding-top: 2px;
			display: block; } }
	.table {
		margin-top: 60px; } }


.referal-panel {
	box-shadow: $bxsh-l;
	padding: 22px 30px 30px;
	border-radius: 10px;
	width: 100%;
	&_title {
		padding-left: 30px;
		position: relative;
		font-weight: 700;
		img {
			position: absolute;
			@include center(v);
			left: -1.5px; } }
	p {
		color: $text-l;
		font-size: 13px;
		font-weight: 500;
		line-height: 1.2;
		padding-top: 7px; }
	form {
		padding-top: 18px; } }

#copy {
	.fRow {
		height: 39px;
		align-items: flex-start; } }

.copy-btn {
	width: 40px;
	padding: 11px 1rem 4px 11px;
	margin-left: 10px;
	cursor: pointer;
	transition: transform 350ms cubic-bezier(.27,.01,.38,1.06), opacity  250ms cubic-bezier(.27,.01,.38,1.06), visibility  1s, background 320ms cubic-bezier(.27,.01,.38,1.06), border  20ms cubic-bezier(.27,.01,.38,1.06), box-shadow 1s ease-out;
	&:hover {
		border-color: #c5c7cf;
		background: #c5c7cf; }
	&:before, &:after {
		bottom: 100%; }
	i {
		color: $text;
		pointer-events: none;
		vertical-align: top; }
	&:active,&:hover,&:focus {
		-webkit-box-shadow: none !important;
		box-shadow: none !important; }
	&:focus {
		border-color: #c5c7cf !important; }
	&.copied {
		&:after,&:before {
			opacity: 1;
			visibility: visible; } } }
