.popup {
	background: #fff;
	box-shadow: 0 7px 40px rgba(0, 0, 0, 0.1);
	padding: 17px 21px 45px;
	border-radius: 10px;
	width: 410px;
	overflow: hidden;
	cursor: default;
	position: relative;
	&_head {
		font-weight: 700;
		margin-bottom: 17px;
		i {
			font-size: 10px;
			color: $icon-l; } }
	&_close {
		float: right;
		cursor: pointer;
		padding: 0 10px;
		margin-right: -10px; }
	&_body {
		font-weight: 500;
		cursor: default;
		padding: 0 14%; }
	&_sub {
		line-height: 21px;
		margin-bottom: 25px; }
	&_image {
		text-align: center;
		img {
			margin: 0 auto; } }
	&_links {
		margin-top: 25px; }
	&_light-sup {
		font-size: 13px;
		color: $text-l;
		font-weight: 600;
		margin-bottom: 1rem; }
	.buttons-wrapper {
		margin-bottom: 25px; }
	.btn {
		margin: 10px auto 0; }
	p {
		text-align: center; }
	.form-group {
		margin-bottom: .95rem; }
	&--win {
		background-image: url(../img/popup-back.svg);
		background-repeat: no-repeat;
		background-position: -90px -45px;
		background-size: cover;
		padding-left: 0;
		padding-right: 0;
		&:before {
			content: '';
			@include coverdiv;
			background-image: radial-gradient(circle 287px at center, #ff9b25 0%, rgba(255, 191, 48, 0) 72%, rgba(255, 205, 52, 0) 100%);
			opacity: .2; }
		.colored {
			padding-right: 12px; }
		.coin-span--blue {
			font-size: 1rem;
			padding: .5px 10px; }
		.popup {
			&_close {
				position: absolute;
				top: 12px;
				right: 20px;
				z-index: 5;
				padding: 3px 10px;
				i {
					font-size: 12px; } }
			&_body {
				padding: 0 53px;
				margin-top: 25px;
				position: relative;
				z-index: 2; }
			&_pl {
				@include coverdiv;
				background: linear-gradient(to bottom,rgba(255,255,255,0) 0,#fffefc 105%);
				z-index: 1; }
			&_head {
				margin-bottom: 0;
				padding: 0 23px;
				z-index: 22;
				* {
					z-index: 2; } }
			&_sub {
				font-size: 13px;
				font-weight: 600;
				padding: 0 10px; } }
		.win-b {
			margin-top: 25px; } }
	.mess {
		margin-bottom: 20px; }
	.c-chance .coin-span {
		padding: 1px; }

	&_oneMore-btn {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 1rem !important;
		.coin-span {
			margin-bottom: -1px; } }
	.colored {
		font-size: 19px;
		text-align: center;
		width: 100%;
		display: block;
		padding-top: 32px; }
	.c-chance {
		opacity: 0;
		height: 0;
		overflow: hidden;
		position: absolute;
		visibility: hidden;
		transform: translateY(20px);
		transition: transform 600ms cubic-bezier(.27,.01,.38,1.06),opacity 500ms cubic-bezier(.27,.01,.38,1.06);
		&.is-visible {
			position: relative;
			transform: translate(0);
			visibility: visible;
			opacity: 1;
			height: auto; } } }


.popup-overlay {
	position: fixed;
	width: 100vw;
	height: 100vh;
	background: rgba(139,143,160,.6);
	top: 0;
	z-index: -1;
	opacity: 0;
	left: 0;
	&.is-active {
		opacity: 1;
		z-index: 99999; } }

.popup {
	transorm: translateY(10px);
	@include tr-fast;
	&:target {
		opacity: 1;
		transform: translate(0); } }



.table {
	background: #fff;
	width: 100%;
	margin-bottom: 1rem;
	background-color: transparent;
	td, th {
		vertical-align: top;
		text-align: left;
		line-height: 1.1; }
	th {
		font-size: 13px;
		font-weight: 600;
		line-height: 1;
		color: $text-l;
		border-bottom: 1px solid #c5c7cf;
		padding-bottom: 1.5rem; }
	tbody {
		td {
			vertical-align: middle;
			font-weight: 500;
			padding-top: 1.5rem; } }
	.coin-span {
		box-shadow: none;
		span {
			padding-left: 33px;
			color: $text-l;
			font-weight: 500;
			&.green {
				color: $green;
				font-weight: 600; } } } }

.table-responsive {
	@include r(767) {
		display: block;
		width: 100%;
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
		-ms-overflow-style: -ms-autohiding-scrollbar; } }


.fancybox-content {
	cursor: default !important; }

.fancybox-bg {
	background: #8b8fa0; }

.fancybox-is-open {
	.fancybox-bg {
		opacity: .6; } }

.fancybox-close-small {
	display: none; }
